import { useQuery } from 'react-query';
import Cookies from 'js-cookie';
import { getConfig } from '../utilities/platformServices';
var getManifestEntitlements = function (uuid) {
    var cs_jwt = Cookies.get('cs_jwt');
    var rhsmAPIBase = getConfig().rhsmAPIBase;
    return fetch(rhsmAPIBase + "/management/v1/allocations/" + uuid + "?include=entitlements", {
        headers: { Authorization: "Bearer " + cs_jwt },
        mode: 'cors'
    })
        .then(function (response) { return response.json(); })
        .catch(function (e) {
        console.error('Error fetching Manifest Entitlements data', e);
    });
};
var useManifestEntitlements = function (uuid) {
    return useQuery(['manifestEntitlements', uuid], function () { return getManifestEntitlements(uuid); });
};
export { useManifestEntitlements as default };
